.presentation {
    text-align: center;
    position: absolute;
    z-index: 1;
    top: 255px;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;

    img{
        border: .5rem solid $blanco;
    }

    h2, h3, p {
        color: $negro;
    }
}

.space {
    height: 300px;
}