/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem; }

p {
  color: #FFFFFF;
  font-size: 2rem; }

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto; }

a {
  text-decoration: none; }

img {
  max-width: 100%; }

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #1a1b15;
  text-align: center; }

h1 {
  font-size: 4rem; }

h2 {
  font-size: 3.6rem; }

h3 {
  font-size: 3rem;
  text-align: center; }

.presentation .perfil {
  width: 200px;
  height: 200px;
  border-radius: 50%; }

.presentation {
  text-align: center;
  position: absolute;
  z-index: 1;
  top: 255px;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto; }
  .presentation img {
    border: 0.5rem solid #FFFFFF; }
  .presentation h2, .presentation h3, .presentation p {
    color: #1a1b15; }

.space {
  height: 300px; }

footer {
  background-image: url("../../../build/img/mountain-6538890_1920.webp");
  margin-bottom: -16px; }
  @media (min-width: 768px) {
    footer {
      display: grid;
      grid-template-columns: 60% auto; } }
  footer .nav {
    display: grid;
    gap: 0.5rem;
    grid-template-columns: repeat(4, 1fr);
    list-style: none; }

.perfil-image {
  background-image: url("../../../build/img/mountain-6538890_1920.webp");
  height: 350px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative; }

.perfil-text {
  text-align: center; }
  @media (min-width: 768px) {
    .perfil-text {
      display: grid;
      grid-template-columns: 60% auto; }
      .perfil-text .nav {
        display: grid;
        gap: 0.5rem;
        grid-template-columns: repeat(4, 1fr); } }
  .perfil-text .nav {
    list-style: none;
    padding-left: 0; }
    .perfil-text .nav li {
      margin-bottom: 0.5rem; }

.blog {
  color: #1a1b15;
  font-weight: 700; }
  @media (min-width: 768px) {
    .blog {
      text-align: start; } }

.redes {
  text-align: center; }
  .redes h2 {
    color: #1a1b15; }
  .redes img {
    width: 55px;
    height: 55px;
    margin-left: 2rem; }

.contact_form {
  width: 460px;
  height: auto;
  margin: 80px auto;
  border-radius: 10px;
  padding-top: 30px;
  padding-bottom: 20px;
  background-color: #fbfbfb;
  padding-left: 30px; }
  .contact_form p {
    font-size: 14px;
    color: #0e0e0e; }
  .contact_form input {
    background-color: #fbfbfb;
    width: 408px;
    height: 40px;
    border-radius: 5px;
    border-style: solid;
    border-width: 1px;
    border-color: #0da6f3;
    margin-top: 10px;
    padding-left: 10px;
    margin-bottom: 20px; }
  .contact_form textarea {
    background-color: #fbfbfb;
    width: 405px;
    height: 150px;
    border-radius: 5px;
    border-style: solid;
    border-width: 1px;
    border-color: #0da6f3;
    margin-top: 10px;
    padding-left: 10px;
    margin-bottom: 20px;
    padding-top: 15px; }
  .contact_form label {
    display: block;
    float: center; }
  .contact_form button {
    height: 45px;
    padding-left: 5px;
    padding-right: 5px;
    margin-bottom: 20px;
    margin-top: 10px;
    text-transform: uppercase;
    background-color: #0da6f3;
    border-color: #0da6f3;
    border-style: solid;
    border-radius: 10px;
    width: 92%;
    cursor: pointer; }
  .contact_form button p {
    color: #fff; }
  .contact_form .aviso {
    font-size: 13px;
    color: #0e0e0e; }
  .contact_form h1 {
    font-size: 39px;
    text-align: letf;
    padding-bottom: 20px;
    color: #1a1b15; }
  .contact_form h3 {
    font-size: 16px;
    padding-bottom: 30px;
    color: #0e0e0e; }
  .contact_form ::-webkit-input-placeholder {
    color: #a8a8a8; }
  .contact_form ::-webkit-textarea-placeholder {
    color: #a8a8a8; }
  .contact_form .formulario input:focus {
    outline: 0;
    border: 1px solid #97d848; }
  .contact_form .formulario textarea:focus {
    outline: 0;
    border: 1px solid #97d848; }

.proyectos {
  max-width: 1000px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 2rem; }
  .proyectos h2 {
    color: #1a1b15; }

.general {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr); }
  @media (min-width: 768px) {
    .general {
      display: grid;
      gap: 1rem;
      grid-template-columns: repeat(3, 1fr); } }
  .general .proy {
    width: 95%;
    height: 350px;
    background: -moz-linear-gradient(-45deg, #e0442c 0%, rgba(125, 185, 232, 0.51) 100%);
    /* FF3.6-15 */
    background: -webkit-linear-gradient(-45deg, #e0442c 0%, rgba(125, 185, 232, 0.51) 100%);
    /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(135deg, #e0442c 0%, rgba(125, 185, 232, 0.51) 100%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#e0442c', endColorstr='#827db9e8',GradientType=1 );
    border-radius: 1rem;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 2rem; }
